	min-width: 120px;
	padding-right: 20px;
	line-height: 34px!important;
}

.formTxt span{
	color:#ff6666;
	padding-right: 3px;
}

.formIpt{
	width: 734px;
	position: relative;
}

.formCode{
	position: relative;
}

.getCode, .getNumber{
	display: block;
    position: absolute;
    left: 128px;
    top: 0px;
    width: 122px;
    height: 34px;
    line-height: 34px;
    text-align: center;
    border-radius: 0 4px 4px 0;
    color: #fff;
}

.getCode{
	background-color: #0099ff;
}

.getNumber{
	background-color: #9D9D9D;
	display: none;
}

.getNumber:hover{
	opacity: 1;
}

.formIpt input:focus,
.formIpt textarea:focus{
	border: solid 1px #45b5ff !important;
	box-shadow: 0px 0px 8px rgba(69, 181, 255, 0.33);
	-moz-box-shadow: 0px 0px 8px rgba(69, 181, 255, 0.33);
}

.formIpt input[type=text],
.formIpt input[type=password],
.formIpt input[type=email],
.fromSelect,
.formIpt input[type=number]{
	width: 250px;
	height: 34px;
	line-height: 34px;
	border: solid 1px #ccc;
	background-color: #fff;
	padding: 0 10px;
	border-radius: 4px;
	-webkit-border-radius: 4px;
	-moz-border-radius: 4px;
	-o-border-radius: 4px;
}

.formIpt input[type=text]:disabled{
	background-color: #eee;
} 

.fromSelect{
	padding: 0 !important;
    overflow: hidden;
    background: url(data:image/png;base64,) no-repeat right #fff;
    cursor: pointer;
}

.fromSelect select{
    background: transparent;
    width: 250px;
    height: 34px;
    padding: 0 10px;
    border: none;
    border-radius: 4px;
	-webkit-border-radius: 4px;
	-moz-border-radius: 4px;
	-o-border-radius: 4px;
    cursor: pointer;
    appearance:none;
	-moz-appearance:none;
	-webkit-appearance:none;
}

.fromSelect select option{
	border-radius: 4px;
	-webkit-border-radius: 4px;
	-moz-border-radius: 4px;
	-o-border-radius: 4px;
	height: 30px;
}

.formIpt span.fromRadio{
	display: inline-block;
	height: 28px;
	line-height: 28px;
	padding: 0 15px;
	position: relative;
	top: 3px;
	cursor: pointer;
	border-radius: 4px;
	-webkit-border-radius: 4px;
	-moz-border-radius: 4px;
	-o-border-radius: 4px;
	border: solid 1px transparent;
	color: #666;
}

.formIpt span.fromRadio:hover{
	opacity: .8;
	border: solid 1px #ccc !important;
}

.formIpt span.radioOn{
	border: solid 1px #33ccff !important;
	color: #0099ff !important;
}

.formIpt span.radioOn:hover{
	border: solid 1px #33ccff !important;
	opacity: .8;
}

.formIpt textarea{
	border: solid 1px #ccc;
	border-radius: 4px;
	-webkit-border-radius: 4px;
	-moz-border-radius: 4px;
	-o-border-radius: 4px;
	padding: 10px;
	background-color: #fff;
	height: 145px;
	min-width: 670px;
	width: 670px;
}

.formIpt label{
	cursor: pointer;
	color: #666;
	line-height: 34px;
	margin-right: 18px;
}

.formIpt label input[type=checkbox]{
	width: 15px;
	height: 15px;
	position: relative;
	top: 3px;
	margin-right: 4px;
	border-radius: 3px;
	-webkit-border-radius: 3px;
	-moz-border-radius: 3px;
	-o-border-radius: 3px;
	border: solid 1px #ccc;
	background-color: #fff;
	-webkit-tap-highlight-color: transparent !important;
	 -webkit-appearance: none;
    appearance: none;
}

.formIpt label input[type=checkbox]:checked::after {
	content: url(data:image/png;base64,);
	background-color: #0099ff;
    position: absolute;
    left: -1px;
    right: -1px;
    top: -1px;
    bottom: -1px;
    border-radius: 3px;
	-webkit-border-radius: 3px;
	-moz-border-radius: 3px;
	-o-border-radius: 3px;
	border: solid 1px #0099ff;
}

.formIpt label input[type=radio]{
	width: 15px;
	height: 15px;
	position: relative;
	top: -2px;
	margin-right: 4px;
    -webkit-appearance: none;
    appearance: none;
    cursor: pointer;
    vertical-align:middle;
    -webkit-border-radius:15px;
    -moz-border-radius:15px;
    border-radius:15px;
    border:1px solid #9d9d9d;
    background-color:#fff;
}

.formIpt label input[type=radio]:checked::after {
    content: " ";
    position: absolute;
    left: -1px;
    right: -1px;
    top: -1px;
    bottom: -1px;
    -webkit-border-radius:15px;
    -moz-border-radius:15px;
    border-radius:15px;
    border: solid 4px #0099ff !important;
}

.formIpt label:hover{
	opacity: .8;
}

.formIpt input.error{
	border: solid 1px #ff6666;
}

.formIpt span.errorTxt{
	color: #FF6666;
	padding: 0 8px;
}

.formIpt button{
	margin-right: 23px;
}

.formIpt p{
	line-height: 34px;
	color: #666;
	display: inline-block;
}
