	color: #fff;
	background-color: #0099FF;
	opacity: 1;
}

.table tbody td span{
	display: inline-block;
	height: 26px;
	line-height: 26px;
	-webkit-border-radius:4px;
    -moz-border-radius:4px;
    border-radius:4px;
    text-align: center;
    position: relative;
    top: 0;
    padding: 0 5px;
}

.table tbody td span.active{
	background-color: #99cc66;
	color: #fff;
	font-size: 12px;
}

.table tbody td span.inactive01{
	background-color: #ff9999;
	color: #fff;
	font-size: 12px;
}

.table tbody td span.inactive02{
	background-color: #ffcc66;
	color: #fff;
	font-size: 12px;
}

.table tbody td span.inactive03{
	background-color: #ccc;
	color: #fff;
	font-size: 12px;
}

.table tbody td span.progressbar{
	height: 8px;
	width: 95px;
	-webkit-border-radius:8px;
    -moz-border-radius:8px;
    border-radius:8px;
    background-color: #dddddd;
    position: absolute;
    top: 20px;
    left: 0;
    padding: 0;
    right: 0;
}

.table tbody td .bar{
	height: 28px;
	position: relative;
}

.table tbody td span.progressbar i{
	display: block;
	height: 8px;
	-webkit-border-radius:8px;
    -moz-border-radius:8px;
    border-radius:8px;
